//3.1 index1.js
import React from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import App from "../../App"; //
import Document from "../../views/Document";
import ReactKnowledge from "../../views/Wirkung/view/p_react_knowledge"; //React
import { react_knowledge_example } from "./ReactKnowledgeDemo";

const router = [
  {
    path: "/document",
    component: Document,
    exact: true
  },
  {
    path: "/wirkung",
    component: Wirkung,
    routes: [ //routes
      {
        path: "/wirkung/to_markdown_edit", //markdown
        component: MarkdownEdit,
        exact: true
      },
      {
        path: "/wirkung/to_reactknowledge", //react
        component: ReactKnowledge,
        routes: [
          {
            path: "/wirkung/to_reactknowledge/to_reactknowledge_demo1",
            component: react_knowledge_example.Demo1,
            exact: true
          },
          {
            path: "/wirkung/to_reactknowledge/to_reactknowledge_demo2",
            component: react_knowledge_example.Demo2,
            exact: true
          },          
        ]
      },
    ]
  },
]

const BasicRoute = () => (
  <HashRouter>    
      <Switch>
        <Route path="/login" component={App} exact></Route> //
        <Route path="/" component={Home} ></Route>
      </Switch>
  </HashRouter>
);

export default BasicRoute;

export{
  router
}
